body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.city {
    color: #626977;
}

.weather-icon {
    width: 100px;
    height: 100px;
}

.box {
    width: 200px;
    height: 300px;
    background: #FFF;
    /* margin: 40px auto; */
    margin: 10px;
    border: 1px solid #ddd;
    border-radius: 2px;
    word-break: break-all;
}

.box:hover {
    background: #ffefde;
}

.box h3 {
    margin: 10px 0 5px 0;
}

.box h4 {
    margin: 0 0 10px 0;
}

.box p {
    margin: 0;
}

.box .close {
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    height: 20px;
    color: #ddd;
    cursor: pointer;
}

.box .close:hover {
    color: #626977;
}

.box .close:active {
    color: #ddd;
}

.effect2 {
  position: relative;
}

.effect2:before, .effect2:after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 15px;
  left: 10px;
  width: 50%;
  top: 80%;
  max-width:300px;
  background: #777;
  box-shadow: 0 15px 10px #777;
  -webkit-transform: rotate(-3deg);
  transform: rotate(-3deg);
}

.effect2:after {
  -webkit-transform: rotate(3deg);
  transform: rotate(3deg);
  right: 10px;
  left: auto;
}
.search-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
}

.search-icon {
  margin: 5px 0 0 -25px;
  color: #b7c0af;
}

.search-input {
  width: 200px;
  padding: 5px 25px 5px 5px;
  border-radius: 10px;
  outline: none;
}

.search-entities {
  border: 1px solid #626977;
  position: absolute;
  background-color: white;
  border-radius: 5px;
  top: 31px;
  width: 240px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: left;
}

.search-entities li {
  padding: 3px;
}

.search-entities li:hover {
  background-color: #c9c3a8;
  color: white;
  border-radius: 5px;
  cursor: default;
}

